import React from 'react';
import { Text } from '../../../../components/Text';
import { AuthorLine } from '../../../../components/AuthorLine';

export default function ENFP() {
    return (
        <>
            <AuthorLine text='Читать 4 минуты' iconName="Time">Виктор Гуленко</AuthorLine>

            <Text tag="p" size="h5" color="role">
                Сильной стороной вашей личности является хорошо развитая интуиция, позволяющая вам безошибочно определять скрытые возможности и способности других людей.
            </Text>

            <Text tag="p">
                Ваша проницательность особенно остро отмечает отрицательные задатки в людях.
            </Text>

            <Text tag="p">
                Больше доверяйте своему первому впечатлению, так как ваша доброта и снисходительность могут заставить вас несколько идеализировать людей при более близком знакомстве.
            </Text>

            <Text tag="p">
                Вы умеете проявлять чуткость и внимание к окружающим, стараетесь помочь им разобраться в личных проблемах, сделать им приятное.
            </Text>

            <Text tag="p">
                Замечая таланты и достоинства людей, вы делаете им удачные комплименты, стараясь тем самым поднять самооценку каждого, улучшить его настроение, а также вселить уверенность в достижении желаемого результата.
            </Text>

            <Text tag="p">
                Другой вашей сильной чертой является умение устанавливать с людьми теплые, доверительные отношения, что располагает их к откровенности.
            </Text>

            <Text tag="p">
                Эту откровенность вы стараетесь употребить им на благо — помочь своими дипломатическими советами выпутаться из трудных жизненных ситуаций, избежать насмешек и поспешных решений, о которых пришлось бы пожалеть в будущем.
            </Text>

            <Text tag="p">
                С другой стороны, если человек не оправдал вашего доверия или вызвал антипатию, вы воздвигаете между ним и собой невидимый барьер и стараетесь отдалиться от него.
            </Text>

            <Text tag="p">
                Ваши антипатии бывают так же сильны, как и симпатии. Но чаще всего вы умело поддерживаете с людьми гармоничные и ровные отношения. Вы — отличный дипломат.
            </Text>

            <hr />

            <Text tag="p" size="h5" color="role">
                 Вашей слабой стороной является неумение заставить себя заниматься долгой кропотливой работой, следовать установленным правилам и регламенту, тщательно вести документацию, подробно прорабатывать детали, работать в тесноте, при большом скоплении людей, а также «уважать» иерархию независимо от индивидуальных качеств начальства.
            </Text>

            <Text tag="p">
                Вам трудно дается беспристрастный логический анализ, отделение главного от второстепенного, из-за чего вы склонны утопать в бесконечных делах и смене увлечений.
            </Text>

            <Text tag="p">
                Вам также трудно правильно распределить мероприятия по времени, что ведет в конечном счете к физическому и нервному переутомлению и неудовлетворенности жизнью.
            </Text>

            <Text tag="p" size="h5" color="role">
                Вам можно рекомендовать следующее: отработайте гибкий, но четкий распорядок дня и старайтесь его соблюдать. 
            </Text>

            <Text tag="p">
                Планируя мероприятия на завтра, обязательно определяйте важность каждого из них.
            </Text>

            <Text tag="p">
                Доверяя своей наблюдательности, помните, что она у вас распространяется лишь на людей, а в деловой сфере прислушивайтесь к советам профессионалов, иначе неизбежны обвинения в верхоглядстве.
            </Text>

            <Text tag="p" size="h5" color="role">
                Больше и вдумчивее читайте, записывайте наиболее важные правила и положения. Помните, на одной импровизации и общем представлении в эпоху НТР далеко не продвинешься.
            </Text>          

            <hr />

            <Text tag="p">
                Другой вашей проблемой является недостаточно развитая сила воли, неумение держать себя в руках при любых обстоятельствах. 
            </Text>

            <Text tag="p" size="h5" color="role">
                Вы часто колеблетесь при принятии важных решений, а неожиданные вспышки агрессивности могут испортить ваши отношения с людьми.
            </Text>

            <Text tag="p">
                Хотя ваша смелость и решительность в экстремальных ситуациях часто выручает вас, но в спокойном состоянии вы периодически ощущаете апатию, ваш тонус падает.
            </Text>

            <Text tag="p">
                Воспитывайте в себе сдержанность и последовательность, заставляйте себя делать все более тщательно и старайтесь доводить начатую работу до конца.
            </Text>

            <Text tag="p">
                Не давите на окружающих без явного на то повода, предлагая навязчивые советы, требуя от них немедленного выполнения чего бы то ни было.
            </Text>

            <Text tag="p" size="h5" color="role">
                Старайтесь управлять своими эмоциями. Вам очень полезны занятия аутотренингом.
            </Text>

            <Text tag="p">
                Если чувствуете, что конфликт неизбежен, старайтесь немедленно прекратить общение, чтобы не сожалеть впоследствии о сказанном или сделанном.
            </Text>

            <Text tag="p" size="h5" color="role">
                Не нервничайте по пустякам.
            </Text>

            <Text tag="p">
                Не сосредоточивайтесь на своих переживаниях, так как это может отрицательно сказаться на вашем здоровье.
            </Text>
        </>
    );
}